import React from 'react';

export const Table:React.FC<{ goods: Good[] }> = ({ goods }) => (
  <table className="table is-striped">
    <thead>
      <tr>
        <th>Id</th>
        <th>Name</th>
        <th>Color</th>
      </tr>
    </thead>

    <tbody>
      {goods.map(good => (
        <tr key={good.id}>
          <td>{good.id}</td>
          <td>{good.name}</td>
          <td>{good.color}</td>
        </tr>
      ))}
    </tbody>
  </table>
);
